header {
	border-bottom: 1px solid #ddd;
	.header-bar {
		background: #EBEBEB;
		padding: 0.5% 0;
		a {
			margin: 0;
			display: inline;
			text-transform: uppercase;
			font-size: 0.5rem;
			transition: all 0.3s ease;
			&:hover {
				color: #888;
			}
		}
		.header-container-left {
			float: left;
		}
		.header-container-right {
			float: right;
			white-space: nowrap;
			a {
				margin-right: 8%;
			}
		}
	}

	.container {
		h1 {
			text-align: center;
			margin: 3% 0;
			a {
				color: #333;
			}
		}
	}

	ul.menu {
		margin: 0;
		padding: 15px 0;
		display: flex;
		justify-content: space-between;
		margin: 0 auto;
		width: 50%;
		li {
			display: inline;
			flex-basis: 8%;
			a {
				color: #444;
				font-family: 'Oswald', sans-serif;
				font-size: 0.8rem;
				text-transform: uppercase;
				white-space: nowrap;
				transition: all 0.3s ease;
				&:hover {
					color: $beige;
				}
			}
		}
	}

}